
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
body {
  font-family: "poppins", sans-serif;
  color: white;
  display: flex;
  flex-direction: column;
  margin: 0;
  background-color: blueviolet;
}
.root{
  display: flex;
  flex-direction: column;
}

.movie-container{
  display: flex;
  position: relative;
  top: 60px;
  justify-content: center;
  flex-wrap: wrap;
}
.movie{
  background-color:rgba(153, 83, 219, 0.955);
  margin: 1rem;
  width: 330px;
  border-radius: 3px;
  box-shadow: 3px 3px 5px rgba(0,0,0,0.1);
  position: relative;
  overflow: hidden;
}
.movie img{
  max-width: 100%;
}
 
.movie-info{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1rem;
}
.heading{
  margin: 0;
}
.movie-overview{
  background-color: rgb(52, 50, 50);
  border-radius: 3px;
  box-shadow: 0px -3px 5px rgba(0,0,0,0.1);
  padding: 1rem;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: auto;
  max-height: 100%;
  transform: translateY(102%);
  transition: transform 0.3s ease-in-out;
}

.movie:hover .movie-overview {
  transform: translateY(0%);
}

.search{
   background-color: transparent;
   color: rgb(255, 255, 255);
   border: 2px solid rgb(95, 18, 162);
   border-radius: 50px;
   font-family: inherit;
   font-size: 1.2rem;
   padding: 0.5rem 1.5rem;
   
}
.tag{
  background-color: blueviolet;
  font-weight: bold;
  border-radius: 5px;
  padding: 0.5rem;
}
.tag.green{
  color:rgb(8, 194, 8);
}
.tag.orange{
  color:orange;

}
.tag.red{
  color:red;

}
form{
  position: relative;
  top: 24px;
  left: 50%;
  transform: translate(-50%,-50%);
  transition: all 1s;
  width:stretch;
  height: 50px;
  background: rgb(162, 90, 231);
  box-sizing: border-box;
  border-radius: 5px;
  padding: 5px;
}

input{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;;
  height: 50px;
  line-height: 30px;
  outline: 0;
  border: 0;
  display: none;
  background-color: rgb(65, 64, 64);
  font-size: 1em;
  text-align: center;
  border-radius: 5px;
  padding: 0 20px;
}



form:hover,
form:valid{
  width: stretch;
  cursor: pointer;
}

form:hover input,
form:valid input{
  display: block;
}


a {
display: none;
position: absolute;
top: 70px;
bottom:0;
left: 0;
right: 0;
font-size: 20px;
color: rgb(0, 0, 0);
text-align: center; 
width: 100%;
}

form:valid a {
display: block;
}